import React from 'react'
import { connect } from 'react-redux'
import { selectDatabase } from 'reducers/root-reducer'

import { plot } from 'sequences'

import ExportButton from 'components/export-button'


const Presentational = props => {
  return (
    <div className="mdl-grid">
      <div className="mdl-layout-spacer"></div>

      <div className="mdl-cell mdl-cell--8-col" id="main" ref={(input) => {
          console.time('plot')
          plot(props.csv)
          console.timeEnd('plot')
        }}>
          <div id="sequence" style={{height: '9em'}}></div>
          <div id="chart"></div>
      </div>

      <div className="mdl-layout-spacer"></div>

      <div className="mdl-cell mdl-cell--2-col" id="sidebar">
        <p>{props.nb_files} fichiers.</p>

        <ExportButton />

        <div><p></p></div>
        <div id="legend" style={{visibility: 'visible', marginTop: '0em'}}><strong>Légende</strong><br /></div>

      </div>

      <div className="mdl-layout-spacer"></div>
      
    </div>
  )
}



const mapStateToProps = state => {
  let database = selectDatabase(state)
  console.time('csv')
  let csv = database.toCsv()
  console.timeEnd('csv')

  return {
    csv,
    nb_files: database.size()
  }
}
 
const mapDispatchToProps = dispatch => {
  return {}
}


const Container = connect(
  mapStateToProps,
  mapDispatchToProps
)(Presentational)

export default Container
